@import url(https://fonts.googleapis.com/css?family=Montserrat:300,400,500,600i,700);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

* {
  padding: 0;
  box-sizing: border-box;
  outline: none; }

body {
  background-color: #e8e8e8;
  font-family: 'Montserrat', sans-serif; }

h1,
h2,
h3,
p {
  margin: 0; }

img {
  display: block;
  height: 100%;
  width: 100%; }

form {
  width: 70%;
  margin: 0 auto; }

input,
button {
  font-family: inherit; }

input {
  display: block;
  background-color: transparent;
  color: #e8e8e8;
  width: 100%;
  padding: .5rem 1rem;
  margin: 1rem 0;
  font-size: 1.2rem;
  border: none;
  border-bottom: 2px solid #f15448; }
  input::-webkit-input-placeholder {
    color: rgba(232, 232, 232, 0.8); }
  input::-ms-input-placeholder {
    color: rgba(232, 232, 232, 0.8); }
  input::placeholder {
    color: rgba(232, 232, 232, 0.8); }
  input:focus {
    background-color: #394f69; }
  input:nth-child(3) {
    margin-bottom: 2.5rem; }

button {
  display: block;
  background-color: #f15448;
  color: #e8e8e8;
  padding: .7rem 1rem;
  margin: 1rem 0;
  font-size: 1.2rem;
  border: none;
  border-radius: .4rem;
  cursor: pointer; }

.wrapper {
  display: flex;
  justify-content: center;
  align-content: center;
  height: 100vh; }

.content {
  display: grid;
  grid-template-columns: 30% auto;
  width: 70%;
  height: 90vh;
  margin-top: 2rem;
  border-radius: 1rem;
  box-shadow: 0 0.7rem 3rem -0.7rem rgba(0, 0, 0, 0.75); }
  .content .right,
  .content .left {
    padding: 1rem; }
  .content .right {
    display: inline-flex;
    flex-flow: column;
    justify-content: center;
    align-items: center;
    background-color: #273648;
    border-top-right-radius: 1rem;
    border-bottom-right-radius: 1rem; }
  .content .left {
    display: flex;
    flex-flow: column;
    justify-content: center;
    align-items: center;
    background-image: -webkit-linear-gradient(bottom, #e2582b, #e2582b, #ff9900);
    background-image: linear-gradient(to top, #e2582b, #e2582b, #ff9900);
    border-top-left-radius: 1rem;
    border-bottom-left-radius: 1rem; }
  .content .title {
    color: #e8e8e8;
    text-align: center; }
    .content .title h1 {
      font-size: 3rem;
      font-weight: 400; }
  .content .location {
    color: #e8e8e8;
    padding-bottom: 1.2rem;
    font-size: 1.2rem;
    text-align: center; }
  .content .temp {
    display: flex;
    justify-content: center;
    color: #e8e8e8;
    padding-bottom: 1rem; }
    .content .temp .degree {
      font-size: 4rem;
      font-weight: 400; }
    .content .temp .scale {
      font-size: 1.2rem;
      padding-top: .8rem;
      padding-left: .4rem; }
  .content .description {
    color: #e8e8e8;
    font-size: 1.2rem;
    text-align: center;
    padding-bottom: 1.5rem; }
  .content .etc {
    display: flex;
    justify-content: space-between; }
  .content .humidity,
  .content .wind {
    color: #e8e8e8;
    font-weight: 400;
    text-align: center; }

.err {
  color: #e8e8e8;
  font-size: 1.5rem; }

.btn-wrap {
  display: flex;
  justify-content: space-evenly; }

.btn {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 15%;
  height: 15%;
  padding: 0; }
  .btn .refresh {
    padding: .5rem;
    height: 100%;
    width: 100%;
    fill: white; }

